<template>


  <div class="layout">
    <Row class="row_project_info">
      <Row class="row_fields" style="width:100%">
        <i-col span=4 class="info_title"><span>项目名称</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.prjName}}</span></i-col>
        
        <i-col span=4 class="info_title"><span>项目编号</span></i-col>
        <i-col span=7 class="info_text"><span class="span_info_item">{{option.prjNum}}</span></i-col>      

        <i-col span="1"> <img class="img-setting"  @click="onClickSetting()"  src="../images/setting.png" /></i-col>
      </Row>
      <Row class="row_fields" style="width:100%">
        <i-col span=4 class="info_title"><span>建设单位</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.proprietor}}</span></i-col>        
        <i-col span=4 class="info_title"><span>评标时间</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.evaluateTime}}</span></i-col>
      </Row>
      <Row class="row_fields" style="width:100%">
        <i-col span=4 class="info_title"><span>招标代理</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.agent}}</span></i-col>      
        <i-col span=4 class="info_title"><span>抽选时间</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.drawTime}}</span></i-col>
      </Row>
      
      <Row class="row_fields"  style="width:100%">      
        <i-col span=4 class="info_title"><span>评标地点</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.address}}</span></i-col>
        <i-col span=4 class="info_title"><span>操作员</span></i-col>
        <i-col span=8 class="info_text"><span class="span_info_item">{{option.operator}}</span></i-col>
      </Row>
    </Row>

    <Row class="row_expert_draw_parent">
      <i-col span=24>
        <Row style="margin-top:10px">
          <i-col span=12 class="col_container"><span>已经抽到的专家</span></i-col>
          <i-col span=6 class="col_container">
            <span style="margin-right:10px">本次抽取专家人数</span>
            <InputNumber  :max="10" :min="1" v-model="expertCount" />
          </i-col>
          <i-col span=6 class="col_container">
            <!-- <Space size="large" name="space_buttons_raido"> -->
              <RadioGroup v-model="radioMulti">
                <Radio label="批量抽选"></Radio>
                <Radio label="单个抽选"></Radio>
              </RadioGroup>
            <!-- </Space> -->
          </i-col>

        </Row>
        <Row class="row_expert_draw">
          <i-col span=12>
            <div class="div-scroll-back">
              <Scroll ref="left_scroll" v-bind:height="300">
                <Table width="90%"  :columns="selectedExpertColumns" :data="selectedExpertList">
                  <template slot-scope="{ index }" slot="action">
                    <Button type="primary" size="small" @click="onClickRemoveSelect(index)">移除</Button>
                  </template>
                </Table>
              </Scroll>
            </div>
          </i-col>
          <i-col span=12>
            <div class="div-scroll-back">

              <Row style="width:100%">
                <i-col span=24>
                  <div class="div_draw_name">
                    <Span class="span_selected_name">{{ slectedName }}</Span>
                  </div>
                </i-col>
              </Row>

            </div>
          </i-col>
        </Row>
        <Row style="width:100%">
          <i-col span=12 class="col_buttons" >
            <!-- <Space size="large" name="space_buttons_left"> -->
              <Label> 请选择保存的模板</Label> <input class="upload_button" type="file" id="doc" />              
              <Button style="margin-left:5px;" type="primary" @click="onClickSave()">保存 </Button> 
              <Button style="margin-left:5px;" type="primary" @click="onClickClearSelected()">清空</Button>
            <!-- </Space> -->
          </i-col>
          <i-col span=12 class="col_buttons"  >
            <!-- <Space name="space_buttons_right"> -->
              <Button type="button" @click="onClickStartDraw()"  class="long_button_start">抽选</Button>
            <!-- </Space> -->
          </i-col>

        </Row>
      </i-col>
    </Row>
    <Row class="row_expert_list">
      <i-col span=24>
        <Card :bordered="false">
          <template #title>
            <Row>
              <i-Col span="4" class="col_caption"><span>专家数据</span></i-Col>
              <i-Col span="8" class="col_caption"><span></span></i-Col>
              <i-Col span="12" class="col_caption">
                <span>导入专家库</span>
                <input class="upload_button" type='file' id='fileLabel' ref='upload' title='请上传xls或者xlsx格式文件'
                  accept='.xls, .xlsx' />
              </i-Col>

            </Row>
          </template>
          <Table width="90%" border :columns="expertColumns" :data="expertPageList">

          </Table>
          <Page class="pageBox" style="margin-top: 30px;text-align: center" :total="pageInfo.pageTotal" :current="pageInfo.page"
            :page-size="pageInfo.limit" @on-change="changePage" />
        </Card>
      </i-col>      
        <p id='pywebview-status'><i>pywebview</i> is not ready</p>
    </Row>
    <div>
      <div class="div_footer" name="foot_tool_bar">        
          <span>技术支持：北京丹程软件有限公司 2022.07</span>
      </div>
    </div>
    <Drawer
            title="设置"
            v-model="pullDrawerLeft"
            width="720"
            :mask-closable="false"
            :styles="styles"

        >
        <Form :model="formData">
                <Row :gutter="32">
                    <Col span="12">
                        <FormItem label="项目名称" label-position="top">
                            <Input v-model="optionEdit.prjName" placeholder="项目名称" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="建设单位" label-position="top">
                            <Input v-model="optionEdit.proprietor" placeholder="建设单位"/>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="32">
                    <Col span="12">
                        <FormItem label="招标代理" label-position="top">
                            <Input v-model="optionEdit.agent" placeholder="招标代理" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="评标地址" label-position="top">
                            <Input v-model="optionEdit.address" placeholder="评标地址"/>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="32">
                    <Col span="12">
                        <FormItem label="项目编号" label-position="top">
                            <Input v-model="optionEdit.prjNum" placeholder="项目编号" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="操作员" label-position="top">
                            <Input v-model="optionEdit.operator" placeholder="操作员"/>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="32">
                    <Col span="24">
                        <FormItem label="评标时间" label-position="top">
                            <Input v-model="optionEdit.evaluateTime" placeholder="评标时间" />
                        </FormItem>
                    </Col>
                </Row>
              </Form>
            <div class="demo-drawer-footer">
                <Button style="margin-right: 8px" @click="onClickCancel()">取消</Button>
                <Button type="primary" @click="onClickSubmit()">提交</Button>
            </div>
      </Drawer>

  </div>
</template>


<script>
import '../js/dateEx'
import XLSX from 'xlsx'
import Docxtemplater from "docxtemplater";
import PizZip from "pizzip";
import PizZipUtils from "pizzip/utils/index.js";
import { saveAs } from "file-saver";

    window.addEventListener('pywebviewready', function(e) {
        var container = document.getElementById('pywebview-status')
        container.innerHTML = '<i>pywebview</i> is ready'
    })
    window.addEventListener('pythonEvent', function (e) {
       var container = document.getElementById('pywebview-status')
        container.innerHTML = e.data.msg + ";" + e.data.value
    }, true);

    function apiGetParam() {
        // pywebview.api.getParam("unitName").then( res=>{
        //     alert( res )
        // })
    }
 

export default {
  watch:{
    slectedNameProx:{
      handler (newName, oldName) {
            this.selectedName = newName
        },
        immediate: true
    }
  },
  data() {
    return {
      formData: {
                    name: '',
                    url: '',
                    owner: '',
                    type: '',
                    approver: '',
                    date: '',
                    desc: ''
                },
      pullDrawerLeft: false,
      option: {
        prjName : '卫生局宿舍一期',
        prjNum: '360121202004200103-SG002',
        proprietor: '南昌县城市建设投资发展有限公司',
        address: '南昌市红谷滩区新民路22号',
        evaluateTime: '2022-07-12',
        drawTime: '2022-07-12 10:28',
        agent: '江西省谷正咨询有限公司',
        operator: '吴平'
      },
      optionEdit: {
        prjName : '',
        prjNum: '',
        proprietor: '',
        address: '',
        evaluateTime: '',
        drawTime: '',
        agent: '',
        operator: ''
      },
      idToDel: 0,
      pageInfo: {
        limit: 10,
        pageTotal: 1,
        page: 1
      },
      expertCount: 5,
      radioMulti: '批量抽选',
      slectedName: "专家抽选",      
      slectedNameProx: "专家抽选",
      checkAllChannels: false,
      pythonMessage: "python message",
      comInstrumentName: "",

      selectedExpertColumns: [
        {
          type: 'selection',
          // width: 60,
          align: 'center',
          fixed: "left",
        },
        {
          title: "编号",
          key: "id",
          // width: 100
        },
        {
          title: "姓名",
          key: "userName",
          // width: 150,
        },
        {
          title: "电话",
          key: "phone",
          // width: 150,
        },
        {
          title: '操作',
          slot: 'action',
          // width: 150,
          align: 'center'
        }
      ],
      selectedExpertList: [
        {
          id: "1",
          userName: "刘伟佳",
          phone: "15180138120",
          unitName: "东华理工大学",
          idCard: ''
        }
      ],
      expertColumns: [
        {
          type: 'selection',
          width: 60,
          align: 'center',
          fixed: "left",
        },
        {
          title: "编号",
          key: "id",
          // width: 100
        },
        {
          title: "姓名",
          key: "userName",
          // width: 150,
        },
        {
          title: "主要专业",
          key: "specialty",
          // width: 200,
        },
        {
          title: "电话",
          key: "phone",
          // width: 150,
        }
        
      ],
      expertList: [{
          id: "1",
          userName: "刘伟佳",
          specialty: "C12其他服务",
          phone: "15180138120",
          unitName: "东华理工大学",
          idCard: "360102196707246379"
        },],

      expertPageList: [
        {
          id: "1",
          userName: "刘伟佳",
          specialty: "C12其他服务",
          phone: "15180138120",
          unitName: "东华理工大学",
          idCard: "360102196707246379"
        }
      ],
      //抽先timer
      timerDraw: null
    }
  },
  methods: {
    
    onClickCancel(){
      this.pullDrawerLeft = false

    },
    
    cloneObject(obj) {
      return JSON.parse(JSON.stringify(obj))
    },
    onClickSubmit(){
      this.option = this.cloneObject( this.optionEdit)
      this.pullDrawerLeft = false
    },
    onClickStartDraw(){
      //  pywebview.api.getParam("unitName").then( res=>{
      //       alert( res )
      //   })
      let that  = this
      if( null != this.timerDraw ){
        this.$Message.info("正在抽取，请稍后再试")
        return
      }
      if( this.expertList.length <= 0 ){
        this.$Message.info("请选导入专家数据")
        return
      }
      if( this.radioMulti =='批量抽选'){
          let nTime = 10
          if( that.selectedExpertList.length < that.expertCount ){
            that.timerDraw = setInterval(() => {
              let user = that.genExpert()
                let item = that.expertList[ user.id-1]
                if(undefined != item ){                          
                  that.slectedNameProx = item.userName 
                  nTime = nTime -1
                  if( nTime <= 0 ){
                    that.selectedExpertList.push( item )
                    nTime = 10
                  }
                  if( that.selectedExpertList.length >= that.expertCount){
                      clearInterval(that.timerDraw)
                      that.timerDraw = null       
                      that.option.drawTime = new Date().Format("yyyy-MM-dd hh:mm")
                  }
                }else {
                  console.log("user is undefined")
              }
              
          }, 10)
          }
          
      }
      else{
        let user = this.genExpert()
        let item = this.expertList[ user.id-1]
        this.slectedName = item.userName      
        this.selectedExpertList.push( item )
        this.option.drawTime = new Date().Format("yyyy-MM-dd hh:mm")
      }
    },
    isSelected( id ){
      let idx = parseInt(id)
      for( var i =0;i< this.selectedExpertList.length;i++){
          let n = parseInt(this.selectedExpertList[i].id)
          if( idx  ==  n ){
            console.log("eq id=" + id +"vs user.id="+ n )
            return true
          }
          else{
            console.log("nq id=" + id +"vs user.id="+ n )
          }
        }
      return false
    },
    genExpert(){
        let idx = this.selectedExpertList.length
        let user = this.genRandom( idx )
        let isSelected = true
        while ( isSelected){
          isSelected = this.isSelected( user.id )
          if( isSelected ){
            user = this.genRandom( idx )
          }
          else{
            break
          }
        }
        return user
    },
    genRandom( seed ){
      let random0 = (Math.random()*1000000 % 313 )* (Math.random()*1000000 % 727)
      let randomA = Math.round ( random0* this.expertList.length + new Date().getUTCSeconds()) % this.expertList.length 
      let randomMs =  new Date().getMilliseconds()* ((Math.random()*1000000) % 100) +  new Date().getFullYear() * new Date().getDate() * new Date().getUTCSeconds()
      randomMs = Math.trunc( randomMs )
      let id = (randomMs + randomA + seed % 5 ) % this.expertList.length 

      
      if( id >= this.expertList.length ){
        id = this.expertList.length-1
      }
      else if( id < 0 ){
        id = 0
      }
      let item = this.expertList[ id ]
      let user = {
         id: item.id,
          userName: item.userName,
          phone: 'phone,' + randomMs + ',' + randomA+"," + id,
          unitName: item.unitName,
      }


      return user
    },
    onClickSetting(){      
      this.optionEdit = this.cloneObject(this.option)
      this.pullDrawerLeft = true
        // pywebview.api.getParam("unitName").then( res=>{
        //          this.slectedName = res
        //       })

    },
    onClickClearSelected(){
      this.selectedExpertList = []
    },
    onClickRemoveSelect( index ){
      let id =  this.selectedExpertList[index].id
      this.idToDel = id
      let arr = this.selectedExpertList.filter( this.checkItemId )
      this.selectedExpertList = arr
    },
    checkItemId( item ){
      let ret = item.id != this.idToDel 
      return ret
    },
    changePage (val) {
      this.pageInfo.page = val
      if( this.pageInfo.page <= 0 ){
        this.pageInfo.page = 1
      }
      let rowStart = this.pageInfo.page * 10 -10;
      let rowEnd = this.pageInfo.page * 10;
      let that = this
      this.expertPageList = []
      for( var i= rowStart;i< that.expertList.length && i <rowEnd;i++ ){
        that.expertPageList.push( that.expertList[i] )
      }
    },

    // 导入数据
    readExcel(e) {
      // 表格导入
      let that = this
      const files = e.target.files
      if (files.length <= 0) {
        // 如果没有文件名
        this.$Message.info("没有选择专家数据文件")
        return false
      } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {       
        this.$Message.error('上传格式不正确，请上传xls或者xlsx格式')
        return false
      }
      document.getElementById('fileLabel').title = files[0].name
      const fileReader = new FileReader()
      fileReader.onload = ev => {
        try {
          const data = ev.target.result
          // 以二进制流方式读取得到整份excel表格对象
          const workbook = XLSX.read(data, {
            type: 'binary'
          })

          that.expertList = [] // 清空接收数据
          let index = 1
          workbook.SheetNames.forEach(element => {
            const wsname = element // 取第一张表
            const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]) // 生成json表格内容
            // 编辑数据
            for (var i = 1; i < ws.length; i++) {
              // {
              //     "江西省谷正咨询有限公司政府采购类专家库": "100",
              //     "__EMPTY": "朱杰斌",
              //     "__EMPTY_1": "B013803高效节能设备（锅炉、风机、水泵、电机等）",
              //     "__EMPTY_2": "13576088106",
              //     "__EMPTY_3": "南昌工程学院",
              //     "__EMPTY_4": "360102196202245813"
              // }
              var sheetData = {
                  id:         index,
                  userName:   ws[i]["__EMPTY"],
                  specialty:  ws[i]["__EMPTY_1"],
                  phone:      ws[i]["__EMPTY_2"],
                  unitName:   ws[i]["__EMPTY_3"],
                  idCard:     ws[i]["__EMPTY_4"],
              }
              if( null!= sheetData.userName && undefined !=  sheetData.userName && !!sheetData.userName.trim() ){
                  that.expertList.push(sheetData)
                  index ++
              }       
            }
          });
          
          that.pageInfo.pageTotal = that.expertList.length
          that.$refs.upload.value = ''
          that.changePage (1)
        } catch (e) {          
          this.$Message.info("导入数据失败：" + e)
          return false
        }
      }
      fileReader.readAsBinaryString(files[0])
      return false
    },
    loadFile(url, callback) {
      PizZipUtils.getBinaryContent(url, callback);
    },
    onClickSave() {
      var docs = document.getElementById("doc");
      if (docs.files.length === 0) {
        this.$Message.info("没有选择模板");
        return
      }
      var reader = new FileReader();
      let docPath = docs.files.item(0)
      reader.readAsBinaryString( docPath );

      reader.onerror = function (evt) {
        console.log("error reading file", evt);        
        this.$Message.info("读模板文件失败: "+ evt);
        return
      };
      const that = this
      reader.onload = function (evt) {
        const content = evt.target.result;
        var zip = new PizZip(content);
        var doc = new Docxtemplater(zip, { paragraphLoop: true, linebreaks: true });

        // Render the document (Replace {first_name} by John, {last_name} by Doe, ...)
        let renderData = {
            "prjName": that.option.prjName,
            "prjNum": that.option.prjNum,
            "address": that.option.address,
            "proprietor": that.option.proprietor,
            "agent": that.option.agent,
            "evaluateTime": that.option.evaluateTime,
            "drawTime": that.option.drawTime,
            "operator": that.option.operator
            
          }
        
        for( var i=1;i<=10;i++){
          let idxName = i + ''
          if (i < 10 ) { 
            idxName = '0' + i
          }
          renderData["userName" + idxName ] = ""         
          renderData["id" + idxName ] = ""            
          renderData["userName" + idxName ] = ""          
          renderData["unitName" + idxName ] = ""            
          renderData["idCard" + idxName ] = ""           
          renderData["phone" + idxName ] = ""  
        }
        let idx = 1
        that.selectedExpertList.filter( e =>{
          let idxName = idx + ''
          if (idx < 10 ) { 
            idxName = '0' + idx
          }
          renderData["userName" + idxName ] = e.userName          
          renderData["id" + idxName ] = e.id          
          renderData["userName" + idxName ] = e.userName          
          renderData["unitName" + idxName ] = e.unitName          
          renderData["idCard" + idxName ] = e.idCard          
          renderData["phone" + idxName ] = e.phone
          idx ++ 
        })
        doc.render(
          renderData
        );

        var out = doc.getZip().generate({
          type: "blob",
          mimeType:
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
          // compression: DEFLATE adds a compression step.
          // For a 50MB output document, expect 500ms additional CPU time
          compression: "DEFLATE",
        });
        // Output the document using Data-URI
        let saveFile = new Date().Format("MMddhhmmss_") + "抽选报告.docx"
        saveAs(out, saveFile);
      };
    }
  },
  mounted() {
    let local = this;
    
    // window.addEventListener('pythonEvent', function (e) {
    //   local.$Message.info(e.data.msg);
    //   local.pythonMessage += "\r\n" + e.data.msg + "\r\n" + e.data.value

    //    var container = document.getElementById('pywebview-status')
    //     container.innerHTML = e.data.msg + ";" + e.data.value
    //     local.slectedName = e.data.msg

    // }, true);

    this.$refs.upload.addEventListener('change', e => {
      // 绑定监听表格导入事件
      this.readExcel(e)
    })
    this.expertList = []
    this.expertPageList = []
    this.option.drawTime = new Date().Format("yyyy-MM-dd hh:mm")
    this.option.evaluateTime = new Date().Format("yyyy-MM-dd hh:mm")
    this.selectedExpertList = []
  },
}
</script>


